@import "../shared/mixins";
@import "../shared/variables";

@import "../../vendor/scss/bootstrap";
@import "../../vendor/scss/font-awesome/font-awesome";


// @import "helper";

@import "animate.min.scss";
@import "owl.carousel";
@import "vegas";
@import "style.scss";

#tinymce {
  text-align: left;
}

.cl {
  clear: left;
}
.cr {
  clear: right;
}
.cb {
  clear: both;
}

h1, h2, h3, h4, h5, h6 {
  strong, b {
    font-family: 'MuseoSlab-700', 'sans-serif';
    font-weight: normal;
  }
}

.admin-bar {
  #main-nav.fixed {
    top: 32px;
  }
}

// As page may have about us slug
body.about-us {
  padding: 0;
}

.ninja-forms-form-wrap {
  padding-left: 150px;
  margin-bottom: 20px;

  .ninja-forms-response-msg {
    padding: 10px 0;
  }

  .ninja-forms-error-msg {
    color: red;
  }
  .ninja-forms-success-msg {
    color: green;
  }

  .ninja-forms-form {
    width: 400px;
    max-width: 100%;
    margin: 0 auto;
    @extend .trans-button;
    @extend .trans-orange;
    font-size: 14px;
  }
  
  .text-wrap.label-above label, .ninja-forms-required-items {
    display: none;
  }

  .field-wrap {
    margin-bottom: 0;
  }

  input {
    border: 0;
    text-align: left;
    text-transform: none;
    padding: 9px;
    min-height: 46px;
    padding-left: 15px;
    display: inline-block;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    background-color: #e1e1e7;
    &:focus, &:active {
      appearance: none;
      border: none;
    }
  }
  .submit-wrap  {
    input {
      display: inline-block !important;
      text-align: center;
      float: right;
      font-family: 'MuseoSlab-300';
      padding: 5px 55px 5px 25px;
      margin: 0px;
      border: none;
      font-size: 22px;
      background-image: url(../img/arrow-green.png);
      background-position: 95% 50%;
      background-repeat: no-repeat;
      background-color: transparent;
      color: #95c11f;
      box-shadow: none;
      &:active {
        outline: 0;
        background-image: none;
      }
    }
  }
}

.newsletter-form-wrap {
  .ninja-forms-form {
    width: 435px;
  }
  .ninja-forms-form-wrap {
    padding: 0;
    .text-wrap {
      float: left;
      width: 250px;
    }

  }
  .submit-wrap input {
    padding: 5px 40px 5px 10px;
  }
}

.focus-box {
  .stepleft, .stepaboutleft {
    font-size: 16px;
  }
}

.white-text {
  h1, h2, h3, h4, h5, h6 {
    color: inherit;
  }
}

.alignleft {
  display: inline;
  float: left;
}

.alignright {
  display: inline;
  float: right;
}

.aligncenter {
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.arrow-left, .arrow-right {
  display: inline-block;
  width: 26px;
  height: 26px;
  background: url('../img/arrow-left.png');
}
.arrow-right {
  background: url('../img/arrow.png');
}

.hero-wrapper {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.post-header {
  padding-top: 70px;
}

.post-content {
  margin-top: 60px;

  h1, h2, h3, h4, h5, h6 {
    color: #152b44;
    font-family: inherit;

  }
  h3 {
    font-size: 20px;
    display: block;
    padding: 15px 0 5px;
  }
  p {
    margin-bottom: 10px;
  }
  
  ul, ol {
    padding-left: 20px;
  }

  li {
    list-style: disc;
    padding-top: 10px;
  }

  ol > li {
    list-style: decimal;
  }

}

.single .post-content {
  margin-top: 0;
}

.how-it-work-steps {
  .content {
    padding-top: 10px;
    font-size: 12px;
  }
}

.get-in-touch {
  .white-text {
    a {
      color: inherit;
    }
  }
  .social {
    >a {
      color: #f9b233;
    }
  }
}

#faq1, #faq2 {
  h3 {
    color: inherit;
    font-size: 22px;
    font-family: 'MuseoSlab-700', 'sans-serif';
    padding-top: 10px;
  }
  h4 {
    font-family: 'MuseoSlab-700', 'sans-serif';
    padding-top: 15px;
    padding-bottom: 5px;
    color: inherit;
  }
}

#faq1 {
  color: #fff;
}


#searchform {
  .form-control {
    width: auto;
    display: inline-block;
    &:hover, &:focus {
      border: 1px solid #ccc;
    }
  }
}

#footer {
  text-align: left;
  color: #fff;
  padding: 25px;
}
@import "responsive.scss";