-----------------------------------------*/

.purchase-now {
  background: #e96656;
  padding-top: 70px;
  padding-bottom: 70px;
}
.purchase-now h3 {
  text-align: left;
  line-height: 40px;
  margin-top: 14px;
}
.purchase-now .red-btn {
  float: right;
  background: #db5a4a;
}
.purchase-now .red-btn:hover {
  background: #bf3928;
}

/*----------------------------------------
 **   SECTION:  CONTACT US             -----
-----------------------------------------*/

.contact-us {
  background-color:white;
  padding-bottom: 95px;
}
.contact-us .input-box,textarea {
  width: 100%;
  margin: auto;
  margin-bottom: 20px;
}
.contact-us .custom-button {
  float: right;
}

/* ----------------------------------------*/
/*   SECTION:  FOOTER                        */
/* ----------------------------------------*/

footer {
  background: #142a43;
}
.company-details {
  color: #939393;
  padding-top: 67px;
  padding-bottom: 67px;
}
.company-details .icon-top {
  font-size: 30px;
  margin-bottom: 10px;
}
.copyright {
  padding-top: 60px;
  padding-bottom: 68px;
  background: #171717;
}
.social li {
  display: inline-block;
  margin: 5px;
}
.social li a {
  color: #939393;
  font-size: 18px;
}
.social li a:hover {
  color: #e96656;
}

/* ========================================= */
/* -----------------------------------------*/
/*   SINGLE PROJECT PAGE                     */
/* -----------------------------------------*/
/* ========================================= */

.single-project {
  text-align: left;
  margin-bottom: 25px;
}
.single-project .project-image {
  width: 100%;
  float: left;
  text-align: left;
  margin-bottom: 25px;
}
.single-project h3 {
  margin-bottom: 10px;
  padding-bottom: 7px;
  line-height: 40px;
  border-bottom: 1px dotted #dadada;
}
.single-project .project-description {
  margin-bottom: 25px;
}
.single-project .button {
  margin-left: 0;
}
.single-project .project-information {
  margin-bottom: 10px;
}
.single-project .project-information ul li {
  border-bottom: 1px dotted #dadada;
  padding-bottom: 5px;
  margin-top: 10px;
}
.single-project .project-information ul li span {
  font-weight: 700;
  margin-right: 5px;
}
