@media (min-width: 992px) and (max-width: 1024px) {
    .navbar-right {
        margin: auto;
        margin-left: 130px;
    }
    .input-box {
        width: 230px;
    }
}
@media (min-width: 768px) and (max-width: 1024px) {
    .mobile-aboutus {
        min-height: 200px;
    }
    .input-box {
        width: 200px;
    }
    /* TOP BAR ELEMENTS */
    
    .responsive-logo {
        position: relative;
    }
    .responsive-logo a {} .responsive-nav {
        padding-top: 15px;
    }
    .responsive-nav li {
        margin-top: 0 !important;
    }
    /* HOME */
    
    .intro {
        margin-top: 20%;
        line-height: 3.438rem !important;
        font-size: 2.813rem;
    }
    /* ABOUT US */
    
    .big-intro {
        text-align: center !important;
    }
    .about-us .column {
        margin-bottom: 40px;
    }
    .skills {
        margin-bottom: 78px !important;
    }
    /* TEAM */
    
    .team-member .profile-pic {
        width: 128px;
        height: 128px;
    }
    .team-member .details {
        display: none;
    }
    /* PURCHASE NOW */
    
    .purchase-now {
        margin: auto;
        text-align: center !important;
    }
    .purchase-now h3 {
        text-align: center;
        margin-bottom: 20px;
    }
    .purchase-now .red-btn {
        float: none;
    }
    /* FOOTER */
    
    footer {
        padding-top: 40px;
    }
    .company-details {
        padding-top: 0;
        padding-bottom: 33px;
    }
    .copyright {
        width: 100%;
        padding-top: 33px;
        padding-bottom: 33px;
    }
    .lg-size {
        font-size: 120px;
    }
}
@media (max-width: 1024px) {
    .focushome {
        padding-top: 0px!important;
    }
    .lnews {
        background-position: 30% 0%;
    }
    .features {
        text-align: center;
    }
    .features .feature-icon {
        float: none;
        margin-right: 0;
        margin-bottom: 20px;
    }
}
